* {
  box-sizing: border-box;
}

body {
  display: grid;
  grid-template-rows: max-content max-content auto max-content;
  grid-template-columns: 100%;
  margin: 0;
  min-height: 100vh;
  color: black;
  font-family: 'Lato', sans-serif;
  line-height: 1.5;
}

a {
  color: inherit;
  text-decoration: none;
}

header {
  padding: 1rem;
  background-color: #efefef;
  font-size: 2em;
}

nav {
  display: flex;
  justify-content: center;
  background-color: #dddddd;
}

.navlink {
  display: block;
  padding: 0.5em 1em;
}
.navlink--current {
  background-color: black;
  color: white;
}

main {
  margin: 0 auto;
  padding: 1rem;
  width: 1000px;
  max-width: 100%;
  background-color: white;
}

img {
  display: block;
  max-width: 100%;
}

h1 {
  font-size: 4em;
}

footer {
  background-color: #efefef;
  padding: 1em;
}
